/**
 * CompareSwiper
 *
 * This component was inspired by @fallsemo (Tristen) at Mapbox
 * https://github.com/mapbox/mapbox-gl-compare
 */

import React from 'react'

class CompareSwiper extends React.Component {

  constructor(props) {
    super(props)

    this.state = {
      dragging: this.props.dragging,
      left: this.props.left,
      windowWidth: window.innerWidth
    }
     this.handleResize = this.handleResize.bind(this)
     this.handleMouseMove = this.handleMouseMove.bind(this)
     this.handleMouseUp = this.handleMouseUp.bind(this)
     this.handleTouchMove = this.handleTouchMove.bind(this)
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize)
    window.addEventListener('mousemove', this.handleMouseMove)
    window.addEventListener('mouseup', this.handleMouseUp)
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize)
    window.removeEventListener('mousemove', this.handleMouseMove)
    window.removeEventListener('mouseup', this.handleMouseUp)
    this.props.handleLeftCompareSwiper(this.state.left)
    window.map.style['clip'] = ''
  }

  handleMouseUp() {
    this.setState({ dragging: false })
  }

  handleResize(e) {
    let x = this.state.left
    let clientWidth = window.innerWidth
    if (x > clientWidth) x = clientWidth
    this.setState({ left: x })
  }

  handleMouseMove(e) {
    if (this.state.dragging) {
      this.setLeft(this.getX(e))
    }
  }

  handleTouchMove(e) {
    if (this.state.dragging) {
      this.setLeft(this.getX(e.touches[0]))
    }
  }

  setLeft(left) {
    this.setState({ left: left })
  }

  getX(e) {
    let x = e.clientX
    let clientWidth = window.innerWidth
    if (x < 0) x = 0
    if (x > clientWidth) x = clientWidth
    return x
  }

  render() {
    const styles = {
      mapboxglCompare : {
        backgroundColor: '#fff',
        position: 'absolute',
        width: '2px',
        height: '100%',
        transform: `translate(${ this.state.left }px, 0px)`,
        zIndex: 10,
        userSelect: 'none'
      },
      compareSwiper : {
        backgroundColor: '#3887be',
        boxShadow: 'inset 0 0 0 2px #fff',
        display: 'inline-block',
        borderRadius: '50%',
        position: 'absolute',
        width: '60px',
        height: '60px',
        top: '50%',
        left: '-30px',
        margin: '-30px 1px 0',
        color: '#fff',
        cursor: 'ew-resize',
      	backgroundImage: 'url(data:image/svg+xml;base64,)'
      }
    }
    if (window.mapRight) {
      window.mapRight.style['clip'] = `rect(0px, 999em, ${ window.innerHeight }px, ${ this.state.left }px)`
    }
    if (window.map) {
      window.map.style['clip'] = `rect(0px, ${ this.state.left }px, 999em, 0px)`
    }

    return (
      <div style={ styles.mapboxglCompare }>
        <div
          style={ styles.compareSwiper }
          onTouchMove={ this.handleTouchMove }
          onTouchEnd={ () => this.setState({ dragging: false }) }
          onMouseDown={ () => this.setState({ dragging: true }) }
          onTouchStart={ () => this.setState({ dragging: true }) }
          onTouchCancel={ () => this.setState({ dragging: false }) }
        ></div>
      </div>
    )
  }
}

CompareSwiper.propTypes = {
  left: React.PropTypes.number,
  dragging: React.PropTypes.bool
}

CompareSwiper.defaultProps = {
  left: window.innerWidth / 2,
  dragging: false
}

export default CompareSwiper



